.slide_viewer {
  height: 100%;
  overflow: hidden;
  position: relative; }

.slide_group {
  height: 100%;
  position: absolute;
  width: 100%; }

.slide {
  display: none;
  height: 100%;
  position: absolute;
  width: 100%;

  &:first-child {
    display: block; }

  &:nth-of-type(1) {
    background: #537895; }

  &:nth-of-type(2) {
    background: #ff6b81; }

  &:nth-of-type(3) {
    background: #7bed9f; }

  &:nth-of-type(4) {
    background: #70a1ff; }

  &:nth-of-type(5) {
    background: #dfe4ea; } }

.slide_buttons {
  display: none;
  left: 0;
  position: absolute;
  right: 0;
  text-align: center; }

a.slide_btn {
  color: #474544;
  font-size: 42px;
  margin: 0 0.175em;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out; }

.slide_btn {
  &.active, &:hover {
    color: #428CC6;
    cursor: pointer; } }

.directional_nav {
  height: 340px;
  margin: 0 auto;
  max-width: 940px;
  position: relative;
  top: -340px; }

.previous_btn {
  bottom: 0;
  left: 100px;
  margin: auto;
  position: absolute;
  top: 0; }

.next_btn {
  bottom: 0;
  margin: auto;
  position: absolute;
  right: 100px;
  top: 0; }

.previous_btn, .next_btn {
  cursor: pointer;
  height: 65px;
  opacity: 0.5;
  -webkit-transition: opacity 0.4s ease-in-out;
  -moz-transition: opacity 0.4s ease-in-out;
  -ms-transition: opacity 0.4s ease-in-out;
  -o-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out;
  width: 65px; }

.previous_btn:hover, .next_btn:hover {
  opacity: 1; }

@media only screen and (max-width: 767px) {
  .previous_btn {
    left: 50px; }

  .next_btn {
    right: 50px; } }










