html {
  --lightestGrey:#f6f6f7 {}
  --lighterGrey:#e8e8e9 {}
  --mediumGrey:#818181 {}
  --black:#000 {}
  --white:#fff {}
  --blue:#245ff6 {}
  --pink:#ef8196 {}
  --green:#6dd86c {}
  --yellow:#edb24a {}
  --dribbble:#ea4c89 {} }

button, input {
  font-family: inherit;
  color: inherit; }

a {
  color: var(--dribbble); }

.l-wrapper {
  position: absolute;
  top: 15%;
  left: 32%;
  padding: 0 1.6rem;
  width: 100%;
  max-width: 48rem;
  display: flex;
  font-family: system-ui;
  font-size: 1.6rem;
  align-items: center;
  justify-content: center;
  background: var(--lightestGrey);
  color: var(--black);
  display: none; }

.c-card {
  background: var(--white);
  border-radius: 0.8rem;
  width: 100%;
  overflow: hidden;
  box-shadow: 0px 10px 20px -2px rgba(0, 0, 0, 0.12); }

.c-card--animating {
  animation: cardBump 300ms ease-out 0s forwards; }

.c-card__body {
  padding: 3.2rem; }

@media screen and (max-width: 512px) {
  .c-card__body {
    padding: 1.6rem; } }

@media screen and (max-width: 512px) {
  .c-card__actions {
    width: 100%; } }

.c-card__title {
  font-size: 2.4rem; }

@media screen and (max-width: 512px) {
  .c-card__title {
    margin-bottom: 1.6rem; } }

.c-control {
  display: flex;
  align-items: center;
  justify-content: space-between; }

@media screen and (max-width: 512px) {
  .c-control {
    flex-direction: column; } }

.c-players {
  display: flex;
  width: 100%;
  min-height: 16rem;
  background: var(--lightGrey);
  transition: all 300ms ease-out 0s; }

.c-player {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  animation: fade 300ms ease-in-out 0s forwards; }

.c-player--exiting {
  animation: fadeOut 200ms ease-out 0s forwards; }

.c-player:before {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: currentColor;
  opacity: 0.25; }

.c-player__icon {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px; }

@media screen and (max-width: 512px) {
  .c-player__icon {
    width: 64px; } }

.c-actions {
  display: flex;
  align-items: center; }

.c-actions__item {
  width: 4rem;
  height: 4rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid transparent;
  background: var(--lighterGrey);
  color: var(--mediumGrey);
  cursor: pointer;
  padding: 0;
  transition: all 120ms ease-out 0s;

  &:hover {
    transform: scale(1.07);
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1); }

  &:focus {
    outline: 0;
    border-color: var(--blue); }

  &:active {
    outline: 0;
    border-color: var(--blue);
    transform: scale(1);
    box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.2);
    border-color: transparent; }

  &[disabled] {
    cursor: not-allowed;
    background: var(--lightestGrey);
    color: var(--lighterGrey);
    box-shadow: none;
    transform: unset;
    user-select: none; } }

@media screen and (max-width: 512px) {
  .c-actions__item {
    border-radius: 0.8rem;
    width: 100%; } }

.c-actions__label {
  font-size: 2.4rem;
  width: 6.4rem;
  text-align: center;
  padding: 0.8rem;
  border: 0;
  margin: 0;
  padding: 0;
  background: transparent;
  user-select: none;
  appearance: none;

  &[disabled] {
    color: inherit;
    appearance: none;
    opacity: 1;
    user-select: none; } }

.u-color--blue {
  color: var(--blue) !important; }

.u-color--pink {
  color: var(--pink) !important; }

.u-color--green {
  color: var(--green) !important; }

.u-color--yellow {
  color: var(--yellow) !important; }

@keyframes fade {
  from {
    opacity: 0;
    width: 0; }

  to {
    opacity: 1; } }

@keyframes fadeOut {
  from {
    opacity: 1; }

  to {
    opacity: 0;
    width: 0; } }

@keyframes cardBump {
  0% {
    transform: scale(1); }

  50% {
    transform: scale(0.98);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); }

  100% {
    transform: scale(1); } }

.c-footer {
